import * as React from 'react';
import User, { Color } from '../User';

import 'whatwg-fetch';

import './Signin.css';

import '../jsencrypt';
import { getUrl } from '../util';

declare const JSEncrypt: any;

const pubKey = `-----BEGIN PUBLIC KEY-----
MIIBIjANBgkqhkiG9w0BAQEFAAOCAQ8AMIIBCgKCAQEA2AWkx61AW3V5Ae5OUvtq
Gz9wcp3zg2ZvH87UoTEIJV1claZvjnSm2yDgKCTu4CgEPPPuyLwZgz7U4Vsygl1C
gXZ+aQMIXJWLFlpm9m6s2YTiUFTfllLhWmPHBx1Ml38fNfIMo1JRSTL7jIhpKxV/
cRS/CZmPJ1KlKtTtfQyhYESx5gSw7yx8oFE057vqGkBXU2LG+aJYym2DtalJqFGb
+BfnWEqwQV9w1Ss6UdS8bFBa6SQSM56ssLyFSCbvc74Hb5Oymq0bXoc9QIBXJc8y
wk2Ms7gA058vBMbmSJ7JCYHT4laXTIin4EgCL9Q7X5/b1S8LI2dR8Xf3oRvsUCYa
CQIDAQAB
-----END PUBLIC KEY-----`;

export default class SignIn extends React.Component<{
	onSignin: (user: User, sessionID: string) => void;
}, {
	username: string,
	password: string,
	loading: boolean,
	error: string
}> {
	public state = {
		loading: false,
		password: '',
		username: '',
		error: ''
	}

	constructor(props: {
		onSignin: (user: User) => void
	}) {
		super(props);

		this.onFormSubmit = this.onFormSubmit.bind(this);

		this.onNameType = this.onNameType.bind(this);
		this.onPasswordType = this.onPasswordType.bind(this);
	}

	public render () {
		return (
			<div className="signin-form-placer">
				<div className="signin-form-box">
					<form
						onSubmit={this.onFormSubmit}
						className="signin-form"
					>
						To sign in, please use the credentials you would use on&nbsp;
						<a target="_blank" href="https://cornerstonepca.ccbchurch.com/index.php">CCB</a>
						{
							this.state.error === '' ?
								null :
								<div
									className="signin-error"
								>
									{this.state.error}
								</div>
						}
						<label>
							Username
						</label>
						<input
							type="text"
							value={this.state.username}
							onChange={this.onNameType}
						/>

						<label>
							Password
						</label>
						<input
							type="password"
							value={this.state.password}
							onChange={this.onPasswordType}
						/>

						<input
							type="submit"
							value="Sign in"
							disabled={
								this.state.password === '' ||
								this.state.username === ''
							}
						/>
					</form>
				</div>
			</div>
		);
	}

	private onNameType (event: React.ChangeEvent<HTMLInputElement>) {
		this.setState({
			username: event.target.value
		});
	}

	private onPasswordType (event: React.ChangeEvent<HTMLInputElement>) {
		this.setState({
			password: event.target.value
		});
	}

	private async onFormSubmit (event: React.FormEvent<HTMLFormElement>) {
		event.preventDefault();

		const encrypt = new JSEncrypt();
		encrypt.setPublicKey(pubKey);
		const password = encrypt.encrypt(this.state.password);

		const res = await fetch(getUrl('/api/signin'), {
			method: 'POST',
			body: JSON.stringify({
				username: this.state.username,
				password
			}),
			headers: {
				'Content-type': 'application/json'
			}
		});

		if (res.status === 400) {
			this.setState({
				error: 'Invalid username or password'
			});
		} else if (!res.ok) {
			this.setState({
				error: 'An unknown error occurred'
			});
		} else {
			const json = await res.json();

			localStorage.setItem('sessionID', json.sessionID);

			const user = {
				id: json.user.id,
				name: json.user.name,
				thumbnailUrl: json.user.thumbnailUrl,
				phoneNumbers: json.user.phoneNumbers,
				email: json.user.email,
				permissions: json.user.permissions,
				color: new Color([
					json.user._color.red,
					json.user._color.green,
					json.user._color.blue
				]),
				_color: json.user._color
			};

			this.props.onSignin(user, json.sessionID);
		}
	}
}


