import * as React from 'react';

import * as io from 'socket.io-client';

import ChannelList from './components/ChannelList';
import Loader from './components/Loader';
import MessageChannel from './components/MessageChannel';
import ThemeChanger from './components/ThemeChanger';
import UserList from './components/UserList';

import User from './User';

import './ChatApp.css';

export interface UserProps {
	user: User
	sessionID: string
	socket: SocketIOClient.Socket
}

let lastChannelID = localStorage.getItem('lastChannel');
if (lastChannelID === null) {
	lastChannelID = 'main';
}

export default class ChatApp extends React.Component<{
	user: User
	sessionID: string
}, {
	channelID: string,
	socket: SocketIOClient.Socket,
	interval: number,
	loading: boolean,
	users: User[] | null,
	theme: string
}> {
	constructor(props: {
		user: User,
		sessionID: string
	}) {
		super(props);

		this.changeChannel = this.changeChannel.bind(this);
		this.onGetUsers = this.onGetUsers.bind(this);
		this.onChangeTheme = this.onChangeTheme.bind(this);

		let theme = 'light';
		if (localStorage.getItem('theme')) {
			theme = localStorage.getItem('theme') as string;
		}

		this.state = {
			channelID: lastChannelID as string,
			socket: io.connect('/', {
				path: '/api/ws'
			}),
			interval: window.setInterval(() => {
				this.state.socket.emit('hearbeat', {
					sessionID: this.props.sessionID
				})
			}, 5 * 60 * 60 * 1000),
			loading: true,
			users: null,
			theme
		};
	}

	public componentDidMount () {
		this.state.socket.emit('authenticate', this.props.sessionID, () => {
			this.setState({
				loading: false
			});
		});
	}

	public componentWillUnmount () {
		this.state.socket.disconnect();
	}

	public render () {
		return (
			this.state.loading ?
				<Loader /> :
				<div
					className={'chatapp ' + this.state.theme}
				>
					<ChannelList
						channelID={this.state.channelID}
						onChannelChange={this.changeChannel}
						user={this.props.user}
						sessionID={this.props.sessionID}
						socket={this.state.socket}
					/>
					<MessageChannel
						channelID={this.state.channelID}
						user={this.props.user}
						sessionID={this.props.sessionID}
						socket={this.state.socket}
						users={this.state.users}
					/>
					<UserList
						user={this.props.user}
						sessionID={this.props.sessionID}
						socket={this.state.socket}
						onGetUsers={this.onGetUsers}
					/>
					<ThemeChanger
						currentTheme={this.state.theme}
						onClick={this.onChangeTheme}
					/>
				</div>
		);
	}

	private changeChannel (channelID: string) {
		this.setState({
			channelID
		});
		this.forceUpdate();
	}

	private onGetUsers (users: User[]) {
		this.setState({
			users
		});
	}

	private onChangeTheme (theme: string) {
		this.setState({
			theme
		});

		localStorage.setItem('theme', theme);
	}
}


