import * as React from 'react';

import './ThemeChanger.css';

interface ThemeChangerProps {
	currentTheme: string
	onClick: (newTheme: string) => void
}

export default class ThemeChanger extends React.Component<ThemeChangerProps, {
	checked: boolean
}> {
	constructor(props: ThemeChangerProps) {
		super(props);

		this.handleCheckboxClick = this.handleCheckboxClick.bind(this);
	
		this.state = {
			checked: this.props.currentTheme === 'dark'
		};
	}

	public render () {
		return (
			<div id="theme-changer-box">
				<input
					type="checkbox"
					id="theme-changer"
					onChange={this.handleCheckboxClick}
					checked={this.state.checked}
				/>
				<label
					htmlFor="theme-changer"
				>
					Dark theme?
				</label>
			</div>
		);
	}

	private handleCheckboxClick (e: React.ChangeEvent<HTMLInputElement>) {
		this.setState({
			checked: e.target.checked
		}, () => {
			this.props.onClick(this.state.checked ? 'dark' : 'light');
		});
	}
}


