import * as React from 'react';

import { UserProps } from '../ChatApp';
import User, { createUser } from '../User';

import './UserList.css';

interface UserListProps extends UserProps {
	onGetUsers: (users: User[]) => void
}

export default class UserList extends React.Component<UserListProps, {
	onlineUsers: User[] | null,
	offlineUsers: User[] | null
}> {
	public state = {
		onlineUsers: null,
		offlineUsers: null
	};

	constructor (props: UserListProps) {
		super(props);

		this.receiveOnlineUsers = this.receiveOnlineUsers.bind(this);
		this.receiveOfflineUsers = this.receiveOfflineUsers.bind(this);
	}

	public componentDidMount () {
		this.props.socket.on('authenticated', ({user}: {user: User}) => {
			createUser(user);

			const onlineUsers = this.getOnlineUsers();
			const offlineUsers = this.getOfflineUsers();

			onlineUsers.push(user);

			for (let i = offlineUsers.length - 1; i >= 0; i--) {
				if (offlineUsers[i].id === user.id) {
					offlineUsers.splice(i, 1);
				}
			}

			this.setState({
				onlineUsers,
				offlineUsers
			});
			
			this.props.onGetUsers([
				...onlineUsers, ...offlineUsers
			])
		});

		this.props.socket.on('client disconnected', (user: User) => {
			createUser(user);

			const onlineUsers = this.getOnlineUsers();
			const offlineUsers = this.getOfflineUsers();

			offlineUsers.push(user);

			for (let i = onlineUsers.length - 1; i >= 0; i--) {
				if (onlineUsers[i].id === user.id) {
					onlineUsers.splice(i, 1);
				}
			}

			this.setState({
				onlineUsers,
				offlineUsers
			});

			this.props.onGetUsers([
				...onlineUsers, ...offlineUsers
			])
		});

		this.props.socket.on('color updates', this.receiveOfflineUsers);
		
		this.props.socket.emit('get online users', this.props.sessionID, this.receiveOnlineUsers);
		this.props.socket.emit('get users', this.props.sessionID, this.receiveOfflineUsers);
	}

	public componentDidUpdate () {
		if (this.state.onlineUsers === null) {
			this.props.socket.emit('get online users', this.props.sessionID, this.receiveOnlineUsers);
		}
		if (this.state.offlineUsers === null) {
			this.props.socket.emit('get users', this.props.sessionID, this.receiveOfflineUsers);
		}
	}

	public render () {
		return (
			<div className="userlist-box">
				<div className="userlist-label">
					Online
				</div>
				{
					this.getOnlineUsers().map((u, i) =>
						<div
							key={i}
							className="online userlist-item"
						>
							<div
								className="image"
								style={{
									backgroundImage: `url(${u.thumbnailUrl})`
								}}
							/>
							<div
								className="name"
							>
								{u.name}
							</div>
						</div>
					)
				}
				<div className="userlist-label">
					Offline
				</div>
				{
					this.getOfflineUsers().map((u, i) =>
						<div
							key={i}
							className="offline userlist-item"
						>
							<div
								className="image"
								style={{
									backgroundImage: `url(${u.thumbnailUrl})`
								}}
							/>
							<div
								className="name"
							>
								{u.name}
							</div>
						</div>
					)
				}
			</div>
		);
	}

	private getOnlineUsers (): User[] {
		if (this.state.onlineUsers === null) {
			return [];
		} else {
			return this.state.onlineUsers as any as User[];
		}
	}

	private getOfflineUsers (): User[] {
		if (this.state.offlineUsers === null) {
			return [];
		} else {
			return this.state.offlineUsers as any as User[];
		}
	}

	private receiveOnlineUsers (onlineUsers: User[]) {
		onlineUsers.map(createUser);

		if (this.state.offlineUsers === null) {
			this.setState({
				onlineUsers
			});
		} else {
			const offlineUsers = this.getOfflineUsers();

			for (let i = offlineUsers.length - 1; i >= 0; i--) {
				let user: User;
				for (user of onlineUsers) {
					if (user.id === offlineUsers[i].id) {
						offlineUsers.splice(i, 1);
					}
				}
			}

			this.setState({
				onlineUsers,
				offlineUsers
			}, () => {
				this.props.onGetUsers([
					...this.state.onlineUsers as any as User[],
					...this.state.offlineUsers as any as User[]
				]);
			});
		}
	}

	private receiveOfflineUsers (offlineUsers: User[]) {
		offlineUsers = offlineUsers.filter(user => user.id !== -1);
		offlineUsers.map(createUser);

		if (this.state.onlineUsers === null) {
			this.setState({
				offlineUsers
			});
		} else {
			const onlineUsers = this.getOnlineUsers();

			for (let i = offlineUsers.length - 1; i >= 0; i--) {
				let user: User;
				for (user of onlineUsers) {
					if (user.id === offlineUsers[i].id) {
						offlineUsers.splice(i, 1);
					}
				}
			}

			this.setState({
				onlineUsers,
				offlineUsers
			}, () => {
				this.props.onGetUsers([
					...this.state.onlineUsers as any as User[],
					...this.state.offlineUsers as any as User[]
				]);
			});
		}
	}
}


