import * as React from 'react';

import Loader from './Loader';
import Message from './Message';

import { UserProps } from '../ChatApp';
import User from '../User';

import './MessageChannel.css';

interface MessageChannelProps extends UserProps {
	channelID: string;
	users: User[] | null;
}

interface MessageData {
	message: string,
	user: User,
	time: number
}

interface CompressedMessage {
	message: string,
	user: number,
	time: number
}

interface InputProps {
	onEnter: (text: string) => void
}

class Input extends React.Component<InputProps, {text: string}> {
	public state = {
		text: ''
	};

	constructor (props: InputProps) {
		super(props);

		this.onChange = this.onChange.bind(this);
		this.onKeyDown = this.onKeyDown.bind(this);
	}

	public render () {
		return (
			<div className="messagechannel-inputbox-center">
				<input
					type="text"
					value={this.state.text}
					onChange={this.onChange}
					onKeyDown={this.onKeyDown}
					placeholder="Enter a message..."
				/>
			</div>
		);
	}

	private onChange (e: React.ChangeEvent<HTMLInputElement>) {
		this.setState({
			text: e.target.value
		});
	}

	private onKeyDown (e: React.KeyboardEvent<HTMLInputElement>) {
		if (e.key === 'Enter' && this.props) {
			this.props.onEnter(this.state.text);
			
			this.setState({
				text: ''
			});
		}
	}
}

export default class MessageChannel extends React.Component<MessageChannelProps, {
	messages: MessageData[] | null,
	channelID: string,
	rawMessages: CompressedMessage[] | null
}> {
	public static getDerivedStateFromProps (props: MessageChannelProps, state: {
		messages: MessageData[],
		channelID: string
	}) {
		if (props.channelID !== state.channelID) {
			return {
				channelID: props.channelID,
				messages: null,
				rawMessages: null
			}
		}

		return null;
	}

	public state = {
		messages: null,
		rawMessages: null,
		channelID: ''
	};

	constructor (props: MessageChannelProps) {
		super(props);

		this.onEnter = this.onEnter.bind(this);
	}

	public componentDidMount() {
		this.props.socket.on('message', (message: MessageData) => {
			const messages = (this.state.messages ? this.state.messages : []) as MessageData[];
			this.setState({
				messages: [...messages, message]
			});
		});

		this.props.socket.on('requested messages', ({
			messages,
			start, 
			finish,
		}: {
			messages: CompressedMessage[],
			start: number,
			finish: number
		}) => {
			const newmessages = ((this.state.messages ?
				(this.state.messages as any as MessageData[]).map(msg => ({
					user: msg.user.id,
					time: msg.time,
					message: msg.message
				})) :
				[]
			) as CompressedMessage[]).slice(0);
			
			for (let i = start; i < finish; i++) {
				newmessages[i] = messages[i - start];
			}

			this.setState({
				rawMessages: newmessages,
			})
		});

		this.props.socket.emit('request messages', {
			chatroomID: this.props.channelID,
			sessionID: this.props.sessionID,
			start: -50
		});
	}

	public componentDidUpdate () {
		if (this.state.rawMessages === null && this.state.messages === null) {
			this.props.socket.emit('request messages', {
				chatroomID: this.props.channelID,
				sessionID: this.props.sessionID,
				start: -50
			});
		} else if (this.state.messages === null && this.props.users !== null) {
			const messages = (this.state.rawMessages as any as CompressedMessage[]).slice(0);

			const finalmessages: MessageData[] = messages.map(message => ({
				message: message.message,
				time: message.time,
				user: (this.props.users as any as User[]).filter(u => u.id === message.user)[0]
			}));

			this.setState({
				messages: finalmessages,
				rawMessages: null
			});
		}
	}

	public render () {
		const messages = this.state.messages ?
			(this.state.messages as any as MessageData[]) : null;

		const newMessages = [] as Array<{
			user: User,
			message: string[],
			time: number
		}>;
		if (messages !== null) {
			messages.forEach(element => {
				if (newMessages.length === 0 || (
					newMessages[newMessages.length - 1].user.id !== element.user.id ||
					newMessages[newMessages.length - 1].time + 3600 < element.time
				)) {
					newMessages.push({
						user: element.user,
						message: [element.message],
						time: element.time
					});
				} else {
					newMessages[newMessages.length - 1].message.push(element.message);
				}
			});
		}

		return (
			<div className="messagechannel-box">
				<div className="messagechannel-message-list">
					{
						newMessages ?
							newMessages.map((message, i) =>
								<>
									{i !== 0 ? <div className="divider" /> : null}
									<div
										key={i}
									>
										<Message
											{...message}
										/>
									</div>
								</>
							) :
							<Loader />
					}
				</div>
				<div className="messagechannel-input-box">
					<Input
						onEnter={this.onEnter}
					/>
				</div>
			</div>
		);
	}

	private onEnter (message: string) {
		this.props.socket.emit('client message', {
			chatroomID: this.props.channelID,
			message,
			sessionID: this.props.sessionID
		});

		this.setState({
			messages: [
				...((this.state.messages ? this.state.messages : []) as MessageData[]),
				{
					message,
					user: this.props.user,
					time: Date.now () / 1000
				}
			]
		});
	}
}


