import * as React from 'react';

import { UserProps } from '../ChatApp';

import './ChannelList.css';

interface ChannelProps extends UserProps {
	channelID: string
	onChannelChange: (channelID: string) => void
}

export default class ChannelList extends React.Component<ChannelProps, {
	channels: string[]
}> {
	public constructor (props: ChannelProps) {
		super(props);

		this.state = {
			channels: [] as string[]
		};
	}
	
	public componentDidMount() {
		this.props.socket.on('requested channels', (channels: string[]) => {
			this.setState({channels});
		});

		this.props.socket.emit('request channels', {
			sessionID: this.props.sessionID
		});
	}

	public render () {
		return (
			<div className="channellist-box">
				{
					this.state.channels.map((channel, i) => 
						<div
							key={i}
							onClick={this.onChannelClick(channel)}
							className={'channellist-item' + (channel === this.props.channelID ? ' selected' : '')}
						>
							{channel}
						</div>
					)
				}
			</div>
		)
	}

	private onChannelClick (channelID: string) {
		return () => {
			this.props.onChannelChange(channelID);
		};
	}
}


