import * as React from 'react';
import User from '../User';

import './Message.css';

const messageDatetimeFormat = {
	weekday: 'short',
	year: 'numeric',
	month: 'short',
	day: 'numeric',
	hour: '2-digit',
	minute: '2-digit'
};

const strip = (text: string): string => {
	const tmp = document.createElement('div');
	tmp.innerHTML = text;
	return tmp.textContent || tmp.innerText || '';
};

const parse = (md: string): string => strip(md)
	.replace(/\*\*\*(.*?)\*\*\*/, '<b><i>$1</b></i>')
	.replace(/\*\*(.*?)\*\*/, '<b>$1</b>')
	.replace(/\*(.*?)\*/, '<i>$1</i>');

export default class Message extends React.Component<{
	message: string[],
	user: User,
	time: number
}> {
	public render () {
		return (
			<div
				className="message-item"
			>
				<div
					className="message-imagedisplay"
				>
					<div
						className="image"
						style={{
							backgroundImage: `url(${this.props.user.thumbnailUrl})`
						}}
					/>
				</div>
				<div className="message-content">
					<div
						className="message-user-stringdisplay"
					>
						<span
							className="message-namedisplay"
							style={{
								color: `rgb(${this.props.user._color.red}, ${this.props.user._color.green}, ${this.props.user._color.blue})`
							}}
						>
							{this.props.user.name}
						</span>
						&nbsp;-&nbsp;
						<span className="message-timedisplay">
							{new Date(this.props.time * 1000).toLocaleString('en-US', messageDatetimeFormat)}
						</span>
					</div>
					{
						this.props.message.map((msg, i) => 
							<div
								key={i}
								className="message-content-part"
								dangerouslySetInnerHTML={{__html: parse(msg)}}
							/>
						)
					}
				</div>
			</div>
		);
	}
}


