import * as React from 'react';

import './App.css';
import ChatApp from './ChatApp';
import Loader from './components/Loader';
import SignIn from './components/Signin';
import User from './User';
import { getUrl } from './util';

import 'whatwg-fetch';

enum View {
	SIGNIN,
	LOADING,
	MESSAGES
}

class App extends React.Component<{}, {
	view: View
	sessionID?: string
	user?: User
}> {
	public state: {
		view: View,
		sessionID?: string,
		user?: User
	} = {
		view: View.LOADING
	};

	public async componentDidMount () {
		const sessionID = localStorage.getItem('sessionID');

		if (sessionID) {
			const res = await fetch(
				getUrl('/api/check'),
				{
					method: 'POST',
					body: JSON.stringify({
						sessionID
					}),
					headers: {
						'Content-type': 'application/json'
					}
				}
			);

			if (res.status === 200) {
				const user = await res.json();

				this.setState({
					user,
					sessionID,
					view: View.MESSAGES
				});
			} else {
				this.setState({
					view: View.SIGNIN
				});
			}
		} else {
			this.setState({
				view: View.SIGNIN
			})
		}
	}

	public render() {
		return (
			<div className="app">
				{
					this.state.view === View.LOADING ?
						<Loader /> :
					this.state.view === View.SIGNIN ?
						<SignIn
							onSignin={this.onSignin}
						/> :
					this.state.view === View.MESSAGES ?
						<ChatApp
							user={this.state.user as User}
							sessionID={this.state.sessionID as string}
						/> : 
						null
				}
			</div>
		);
	}

	private onSignin = (user: User, sessionID: string) => {
		this.setState({
			user,
			sessionID,
			view: View.MESSAGES
		});
	}
}

export default App;



